@import nulstyle


body
	color: #ffffff
	background-color: #162831
	font-family: 'Montserrat', sans-serif
a
	color: #ffffff
li
	list-style-type: none
button
	color: #ffffff
.container
	max-width: 1330px
	width: 100%
	padding: 0 15px
	margin: 0 auto
.header__top
	display: flex
	justify-content: space-between
	align-items: center
	padding: 30px 0 50px 0
	transition: .3s
.header__list
	display: flex
	justify-content: space-between
.burger-box
	display: flex
	justify-content: space-between
	align-items: center
	
.header__link
	font-weight: 400
	font-size: 14px
	line-height: 135%
	padding-right: 40px
.button
	font-weight: 600
	font-size: 13px
	line-height: 140%
	background-color: #018ABE
	padding: 9px 15px
	border-radius: 3px
	transition: .3s
	&:hover
		background-color: #fff
		color: #018ABE
.header__mind
	background-image: url(../images/background.png)
	background-repeat: no-repeat
	background-position: center
	background-size: cover
	text-align: center
	border-radius: 10px
.header__lips
	display: flex
	align-items: center
	justify-content: center
	text-align: center
.header__lips-text
	font-weight: 700
	font-size: 24px
	line-height: 140%
	padding: 0 66px
	font-family: 'El Messiri'
.lips
	width: 28px
	height: 14px
.header-title
	font-weight: 600
	font-size: 64px
	line-height: 140%
	font-family: 'El Messiri'
	letter-spacing: 2px
.mind__body
	padding: 136px 0 210px 0


/*-=-=-=-=-=-=-=-=-=-=*/


.block-1
	padding: 150px 0
.block-1__lips-text
	padding: 0 66px
	font-weight: 700
	font-size: 42px
	line-height: 140%
	font-family: 'El Messiri', sans-serif
.block-1__grid
	display: grid
	grid-template-columns: 1fr 1fr 1fr
	grid-template-rows: 284px 284px
.block-1__grid-b
	background-image: url(../images/label-2.png)
	background-repeat: no-repeat
	height: 280px
	display: flex
	align-items: flex-end
	padding: 10px
.block-1__grid-b-2
	background-image: url(../images/label-bg.png)
	background-repeat: no-repeat
	height: 540px
.block-1__grid-b:nth-child(4)
	grid-column-start: 2
	grid-row-start: 2
.block-1__grid-b:nth-child(5)
	grid-column-start: 3
	grid-row-start: 2
	grid-gap: 24px
.block-1__grid-text
	font-weight: 700
	font-size: 24px
	line-height: 140%
	font-family: 'El Messiri'
	letter-spacing: 1px
.grid-text-2
	margin-bottom: 22px
.header__lips-2
	padding-bottom: 100px


/*-=-=-=-=-=-=-*/

.block-2
	padding-bottom: 150px
.slider
	position: relative
.slider__column
	background: #018ABE
	border-radius: 10px
.slider__column-body
	padding: 40px 45px
.slider__title
	font-weight: 700
	font-size: 14px
	line-height: 140%
	padding-bottom: 10px
.slider__text
	font-weight: 300
	font-size: 10px
	line-height: 140%
	max-width: 310px
	height: 180px
	overflow-y: hidden
.slick-track
	display: flex
	align-items: center
.slick-slider
	overflow: hidden
.slick-slide
	margin-right: 50px
	height: 276px
	opacity: 0.5
.slick-slide .slider__text
	font-size: 10px
.slick-slide.slick-center.slick-active
	min-width: 636px
	height: 350px
	opacity: 1
.slick-slide.slick-center .slider__text
	font-size: 14px
	min-width: 516px
.slick-slide.slick-center .slider__column-body
	padding: 50px 60px 56px 60px
.slick-slide.slick-center .slider__title
	font-weight: 700
	font-size: 20px
	line-height: 140%
	padding-bottom: 20px
.slick-arrow.slick-prev
	position: absolute
	top: 37px
	height: 276px
	width: 300px
	background-color: #000
	z-index: 99
	opacity: 0
.slick-arrow.slick-next
	position: absolute
	top: 37px
	height: 276px
	width: 300px
	background-color: #000
	z-index: 99
	opacity: 0
	right: 0
.slick-dots
	display: flex
	justify-content: center
	margin-top: 60px
.slick-dots li
	font-size: 0
	height: 10px
	width: 10px
	background-color: #fff
	opacity: 0.4
	margin-right: 20px
.slick-dots li.slick-active
	opacity: 1


/*-=-=-=-=-=-=-=-=-==-=-=-=-=-=-*/


.block-3__columns
	max-width: 1076px
	width: 100%
	margin-left: auto
	margin-right: auto
.block-3__column
	display: flex
	justify-content: space-between
	align-items: center
.block-3__column-title
	font-weight: 700
	font-size: 24px
	line-height: 140%
.block-3__column-text
	font-weight: 300
	font-size: 16px
	line-height: 140%
	padding-top: 30px
	max-width: 466px
	width: 100%
	padding-right: 30px
	padding-left: 0
.block-3__column:nth-child(1)
	padding-bottom: 100px
.block-3__img
	max-width: 526px
	width: 100%
.block-3__column-list
	padding: 0 20px
	max-width: 526px
	width: 100%



/*-=-=-=-=-=-=-=-=-=-=-=-=-=*/


.block-4 .header__lips
	padding-top: 150px
.block-4__columns
	display: flex
	justify-content: space-between
	align-items: center
	padding-bottom: 150px
.block-4__column:nth-child(1)
	width: 416px
.block-4__column:nth-child(2)
	text-align: center
.block-4__column-list
	display: flex
	align-items: center
.block-4__column-box
	display: flex
	justify-content: space-between
	padding-bottom: 31px
	align-items: center
.block-4__column-list-text
	padding-left: 20px
	font-weight: 400
	font-size: 18px
	line-height: 140%
.block-4__column-text
	font-weight: 700
	font-size: 18px
	line-height: 140%
.block-4__img
	max-width: 526px
	width: 100%
.block-4__column-text-2
	padding-top: 30px
.block-4__columns
	flex-wrap: wrap
	justify-content: center
.block-4__img
	margin-left: 20px

/*-=-=-=-=-=-=-=-=-=-=-=-=*/


.block-5__body
	display: flex
.block-5__columns
	padding-left: 48px
.block-5__column
	padding-bottom: 24px
.block-5__column-box
	background: #018ABE
	border-radius: 10px
	padding: 35px 45px
.block-5__column-box-title
	font-weight: 700
	font-size: 24px
	line-height: 140%
	padding-bottom: 20px
.block-5__column-box-list
	display: flex
	align-items: center
	padding-bottom: 21px
.block-5__column-box-text
	font-weight: 400
	font-size: 14px
	line-height: 135%
	padding-left: 20px
.block-5__column-box-button
	font-weight: 600
	font-size: 13px
	line-height: 140%
	color: #018ABE
	background-color: #fff
	padding: 9px 15px
	border-radius: 3px
	transition: .3s
	cursor: pointer
	&:hover
		background-color: #162831
		color: #fff
iframe
	border-radius: 10px
	width: 856px
	height: 585px

/*-=-=-=-=-=-=-=*/


.footer
	padding: 90px 0 30px 0
.footer-list
	display: flex
	justify-content: space-between
.footer-text
	font-weight: 400
	font-size: 14px
	line-height: 135%
.footer-text-2
	font-weight: 700
	font-size: 14px
	line-height: 135%


.checkbox
	display: none
.burger
	display: none

@media(max-width: 1300px)
	.block-1__grid
		grid-template-columns: 1fr 1fr
		grid-template-rows: 284px 284px 540px
		margin-right: auto
		margin-left: auto
		max-width: 880px
	.block-1__grid-b-2
		height: 540px
	.block-1__grid-b:nth-child(4)
		grid-column-start: 2
		grid-row-start: 1
	.block-1__grid-b:nth-child(5)
		grid-column-start: 2
		grid-row-start: 2
		grid-gap: 24px
	.block-1__grid-b:nth-child(3)
		grid-column-start: 1
		grid-row-start: 2
	.block-1__grid-b:nth-child(2)
		grid-column-start: 1
		grid-row-start: 1
	.block-1__grid-text
		font-size: 24px
	.grid-text-2
		margin-bottom: 22px
	.header__lips-2
		padding-bottom: 100px
@media(max-width: 1200px)
	.block-5__body
		display: flex
		flex-direction: column
	.block-5__column
		min-width: 300px
	.block-5__columns
		display: flex
		flex-wrap: wrap
		justify-content: space-between
		max-width: 800px
		width: 100%
		margin-left: auto
		margin-right: auto
		padding-top: 30px
		padding-left: 0
	iframe
		margin-left: auto
		margin-right: auto
		display: block
		max-width: 856px
	.block-5__column:nth-child(2)
		padding-left: 10px
	.block-5__column-box
		height: 335px
	.block-5__column
		max-width: 300px
@media(max-width: 1100px)
	
	.block-3__column-title
		font-size: 20px
	.block-3__column-text
		font-size: 16px
		width: 400px
	.block-3__column
		flex-wrap: wrap
		justify-content: center
	.block-3__column-list
		padding: 0 0 30px 0
	.block-3__column-list:nth-chiild(2)
		order: 1
	.block-3__img
		order: 2
@media(max-width: 950px)
	iframe
		max-width: 556px
	.slider__text
		display: none
	.slick-slide
		min-width: 130px
		height: 130px
		margin-right: 20px
	.slick-slide.slick-center.slick-active
		min-width: 436px
		height: 450px
		opacity: 1
	.slick-slide.slick-center .slider__text
		font-size: 14px
		min-width: 356px
		height: 100%
		display: block
	.slick-slide.slick-center .slider__column-body
		padding: 40px 50px 46px 50px
	.slick-slide.slick-center .slider__title
		font-weight: 700
		font-size: 20px
		line-height: 140%
		padding-bottom: 20px
	//
	.block-4__img
		margin-left: 0px
	.header__button
		margin-right: auto
		margin-left: auto
		padding-right: 60px
	.burger
		display: block
		position: absolute
		top: 35px
		right: 20px
		border: 2px solid #fff
		border-radius: 50%
		border: 2px solid #fff
		padding: 13px 11px 11px 11px
		z-index: 55
		height: 50px
		width: 50px
	.header__list
		display: flex
		padding-top: 20px
		text-align: right
		li
			padding-bottom: 30px
	li:nth-child(2)
		padding-left: 30px
	li:nth-child(3)
		padding-left: 30px
	.header__link
		background-color: #fff
		color: #018ABE
		padding: 5px
		border-radius: 3px
		transition: .3s
		cursor: pointer
		&:hover
			background-color: #162831
			color: #fff
	.burger-box
		flex-direction: column
		z-index: 5
		top: 100px
		right: 0
		left: 0
		opacity: 0
		margin: auto
		visibility: hidden
		position: absolute
		transition: .3s
	.burger-line
		display: block
		height: 2px
		width: 25px
		border-radius: 50px
		background-color: #fff
		transition: .3s
	.burger-line:nth-child(2)
		margin: 7px 0
	.checkbox:checked ~ .header__top
		padding-bottom: 200px
	.checkbox:checked ~ .header__top .burger-box
		opacity: 1
		visibility: visible
	.checkbox:checked ~ .header__top .burger .burger-line:nth-child(2)
		background-color: transparent
	.checkbox:checked ~ .header__top .burger .burger-line:nth-child(1)
		transform: rotate(-45deg)
		position: absolute
		width: 35px
		height: 3px
		top: 21px
		right: 5px
	.checkbox:checked ~ .header__top .burger .burger-line:nth-child(3)
		transform: rotate(45deg)
		position: absolute
		height: 3px
		width: 35px
		top: 21px
		right: 5px
@media(max-width: 880px)
	.block-1__grid
		grid-template-columns: 1fr
		grid-template-rows: 284px 284px 284px 284px 540px
		max-width: 400px
		width: 100%
	.block-1__grid-b-2
		height: 540px
	.block-1__grid-b:nth-child(4)
		grid-column-start: 1
		grid-row-start: 1
	.block-1__grid-b:nth-child(5)
		grid-column-start: 1
		grid-row-start: 2
		grid-gap: 24px
	.block-1__grid-b:nth-child(3)
		grid-column-start: 1
		grid-row-start: 3
	.block-1__grid-b:nth-child(2)
		grid-column-start: 1
		grid-row-start: 4
	.block-1__grid-text
		font-size: 24px
	.grid-text-2
		margin-bottom: 22px
	.header__lips-2
		padding-bottom: 100px
	.block-1__grid-text
		font-weight: 700
		font-size: 16px
	//
	
@media(max-width: 800px)
	.block-4__column-list-text
		font-size: 16px
	.block-4__column-text
		font-size: 16px
