	.slick-slide.slick-center.slick-active
		min-width: 336px
		height: 570px
		opacity: 1
	.slick-slide.slick-center .slider__text
		font-size: 14px
		min-width: 256px
		height: 100%
		display: block
	.slick-slide.slick-center .slider__column-body
		padding: 40px 50px 46px 50px
	.slick-slide.slick-center .slider__title
		font-weight: 700
		font-size: 20px
		line-height: 140%
		padding-bottom: 20px
@media(max-width: 650px)
	.slick-slide.slick-center.slick-active
		width: 100%
		height: 100%
