		text-align: center
	.slick-slide.slick-center .slider__text
		font-size: 14px
		max-width: 500px
		width: 100%
		height: 100%
		display: block
	.slick-slide.slick-center .slider__column-body
		padding: 20px 20px 26px 20px
	.slick-slide.slick-center .slider__title
		font-weight: 700
		font-size: 20px
		line-height: 140%
		padding-bottom: 20px
@media(max-width: 660px)

	iframe
		height: 485px
	
	.block-4__column-text
