@media(max-width: 630px)
	iframe
		max-width: 356px
	.block-5__columns
		justify-content: space-around
		margin-left: auto
		margin-right: auto
	.block-5__column:nth-child(2)
		padding-left: 0px
@media(max-width: 540px)
	.footer-text,
	.footer-text-2
		font-size: 12px
	.header-title
		font-size: 50px
	.header__lips-text
		padding: 0 26px
