	.block-3__column-text
		width: 100%
@media(max-width: 480px)
	iframe
		max-width: 300px
	.slick-slide.slick-center.slick-active
		min-width: 256px
		height: 100%
	.slick-slide.slick-center .slider__text
		min-width: 216px
@media(max-width: 380px)
	.header-title
		font-size: 40px